.help-container {
  z-index: 100;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background-color: rgba(0, 0, 0, 0.5);

  .help {
    z-index: 100;
    position: absolute;
    left: 50%;
    top: 15%;

    transform: translate(-50%, 0);

    overflow-y: scroll;

    width: 50%;
    height: 50%;
    padding: 1rem;
    border-radius: 0.5rem;

    background-color: white;

    h1 {
      margin: 1rem 0 2rem 0;
    }
  }
}
